import React from 'react'
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';
import { getProfitLoss, getTrialBalance } from '../../api/accounting/reports'
import Loader from '../../App/components/Reusables/Loader'
import Num from '../../App/components/Reusables/Num';
import Jhilikka from '../../App/components/Reusables/Jhilikka';



function TrialBalance(props) {
    const [loading, setLoading] = React.useState(true)
    const [expenses, setExpenses] = React.useState({})
    const [revenues, setRevenues] = React.useState({})
    const [trialBalanceData, setTrialBalanceData] = React.useState({})


    React.useEffect(() => {

        const controller = new AbortController();
        (async () => {
            await getProfitLoss({ action: 'get', filter: 'none', start: 0, end: 999999 }, controller.signal)
                .then(async (res) => {
                    if (res.json.status) {
                        const revs = res.json.revenue;
                        const exps = res.json.expense;
                        const liabs = res.json.liabilities;
                        const asts = res.json.assets;
                        revs.meta_data.sum_current_amount *= -1;
                        await setExpenses({ ...res.json.expense })
                        await setRevenues({ ...revs })
                        setTrialBalanceData([
                            ...revs.sub_headers,
                            ...asts.sub_headers,
                            ...liabs.sub_headers,
                            ...exps.sub_headers,
                        ])
                        await setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
        })()
        return () => controller.abort()
    }, [])

    const _expand = (index) => {
        const tempTrialBalance = trialBalanceData;
        tempTrialBalance[index].isExpanded = false;
        setExpenses({ ...tempTrialBalance })
    }

    const _collapse = (index) => {
        const tempTrialBalance = trialBalanceData;
        tempTrialBalance[index].isExpanded = true;
        setExpenses({ ...tempTrialBalance })
    }

    let drTotal = 0;
    let crTotal = 0;
    if (!loading) {
        var List = (input, type) => {
            return input.map((aRow, index) => {
                let ButtonElement = null;
                if (!aRow.isExpanded) {
                    ButtonElement =
                        <i
                            className="feather icon-minus-square text-c-green f-14 pl-1 pr-1"
                            style={{ color: '#10a9f5', cursor: 'pointer' }}
                            onClick={(e) => _collapse(index)}
                        />
                } else {
                    ButtonElement = <i
                        className="feather icon-plus-square text-c-green f-14 pl-1 pr-1"
                        style={{ color: '#10a9f5', cursor: 'pointer' }}
                        onClick={(e) => _expand(index)}
                    />
                }



                const AccountsList = aRow.accounts.map((ac, inx) => {
                    if (ac.current_amount >= 0) {
                        drTotal += ac.current_amount;
                    } else {
                        crTotal -= ac.current_amount;
                    }
                    if (ac.current_amount === 0) return null;
                    return <tr onClick={() => props.history.push('/account/' + ac.uuid)} key={inx + 'r'} className='balanceSheetChildRow'>
                        <th></th>
                        <td className='balanceSheetChildData'>
                            {ac.name}
                        </td>

                        {ac.current_amount >= 0 ?
                            <>
                                <td className='balanceSheetChildData'><Num>{ac.current_amount}</Num></td>
                                <td></td>
                            </> :
                            <>
                                <td></td>
                                <td className='balanceSheetChildData'><Num>{Math.abs(ac.current_amount)}</Num></td>
                            </>
                        }
                    </tr >
                })
                if (aRow.meta_data.sum_current_amount === 0) return null;
                return [<tr key={index} >
                    <th></th>
                    <td>
                        {aRow.name}
                        {aRow.accounts.length > 0 ? ButtonElement : null}
                    </td>


                    {aRow.meta_data.sum_current_amount >= 0 ?
                        <>
                            <td ><Num>{aRow.meta_data.sum_current_amount}</Num></td>
                            <td></td>
                        </> :
                        <>
                            <td></td>
                            <td ><Num>{Math.abs(aRow.meta_data.sum_current_amount)}</Num></td>
                        </>
                    }
                </tr>,
                ...(!aRow.isExpanded ? AccountsList : [])
                ]
            })
        }
    }


    if (loading) return <Loader />;
    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Trial Balance</h4>
                <div>
                    <span></span>
                </div>
            </Row>
            <br />
            <Row>
                <Col>
                    <Card>
                        {/* <h5 style={{ textAlign: 'center', marginTop: '30px' }}>{(new Date()).toDateString()}</h5> */}
                        <Card.Body>
                            <Table className="accountingReportTable" hover responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Particulars</th>
                                        <th>Debits</th>
                                        <th>Credits</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {List(trialBalanceData, 'trialBalanceData')}
                                    <tr >
                                        <th></th>
                                        <th><u>Total</u></th>
                                        <th><u><Num>{drTotal}</Num></u></th>
                                        <th><u><Num>{crTotal}</Num></u></th>
                                    </tr>
                                </tbody>
                            </Table>
                        </Card.Body>
                    </Card >
                </Col>
            </Row>
        </>
    )
}

export default TrialBalance
